.App {
  text-align: left;
  Color: #ebebeb;
      font-size: 18px;
}
Body {
	background-color: #585858;
	padding-left: 40%;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.text {
	font-size: 22px;
	height: 10px;
	padding-bottom: 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #4e4d4a;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.icon {
	padding-top: 10px;
}

.Top {
	text-align: left;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.p {
	padding-left: 30px;
}